// One-off special link helper as a bridge until v6
.link-body {
  @extend %link-color;
  --#{$prefix}link-main-color-rgb: var(--#{$prefix}fg-main-rgb);

  &:hover,
  &:focus {
    --#{$prefix}link-main-color-rgb: var(--#{$prefix}fg-main-rgb);
  }

  &:active {
    --#{$prefix}link-main-color-rgb: var(--#{$prefix}link-main-rgb);
  }

}

.stretched-link {
  &::#{$stretched-link-pseudo-element} {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: $stretched-link-z-index;
    content: "";
  }
}


.link-icon {
  display: inline-flex;
  gap: $icon-link-gap;
  align-items: center;
  text-decoration-color: rgba(var(--#{$prefix}link-main-color-rgb), var(--#{$prefix}link-opacity, .5));
  text-underline-offset: $icon-text-underline-offset;
  backface-visibility: hidden;

  > .icon {
    flex-shrink: 0;
    width: $icon-link-icon-size;
    height: $icon-link-icon-size;
    fill: currentcolor;
    @include transition($icon-link-icon-transition);
  }
}

.link-icon-hover {
  &:hover,
  &:focus-visible {
    > .icon {
      transform: var(--#{$prefix}icon-link-transform, $icon-link-icon-transform);
    }
  }
}
